<template>
	<div class="main1">
		<div v-wechat-title="$route.meta.title"></div>
        <!--活动详情页面-->
        <div class="content1">
            <div class="title">
                <div class="developTitle">
                    <div class="kuai"></div>
                    <div class="title1">主题</div>
                </div>

                <div class="productTitle">{{theme}}</div>

            </div>
            <div class="gameDetail">
                <div class="gameTitle">
                    <div class="kuai"></div>
                    <div class="title1">活动时间</div>
                </div>
                <div class="gameContent" >
                    &ensp;{{activityTime}}
                </div>
            </div>
            <div class="gameDetail">
                <div class="gameTitle">
                    <div class="kuai"></div>
                    <div class="title1">活动地点</div>
                </div>
                <div class="gameContent" >
                    &ensp;{{activityAddress}}
                </div>
            </div>
            <div class="gameDetail">
                <div class="gameTitle">
                    <div class="kuai"></div>
                    <div class="title1">活动内容</div>
                </div>
                <div class="gameContent" v-html="activityContent">

                </div>
            </div>
        </div>
        <div class="signUpOnline">
            <button v-if="!$route.query.xcx" @click="goDetail('/activitiesSignUp?activityId='+$route.query.id+'&isOfficialAccount=true')">活动报名</button>
            <button v-if="$route.query.xcx" @click="goDetail('/activitiesSignUp?activityId='+$route.query.id+'&agentId='+$route.query.agentId+'&agentName='+$route.query.agentName+'&agentType='+$route.query.agentType+'&agentCode='+$route.query.agentCode)">活动报名</button>
        </div>
	</div>
</template>

<script>
	import request from '../../../../static/js/request'
	import result from '../../../../static/js/result'
    import Swiper from '../../../../static/js/swiper/swiper';
	export default{
		data() {
			return {
                theme:'',//主题
                activityAddress:'',//活动地点
                activityContent:'',//活动详情内容
                activityTime:''//活动时间
			}
		},
		mounted() {

		},
		created() {
            this.$nextTick(()=>{
                new Swiper(this.$refs['swiper-container'],{
                    pagination: {
                        el: this.$refs['swiper-pagination'],
                        clickable :true
                    },
                    autoplay: {
                        delay: 3000,
                        stopOnLastSlide: false,
                        disableOnInteraction: false,
                    },
                })
            })
            this.getDetail();
		},
		methods: {
            /**
             * 报名
             * **/
            goDetail(url){
                this.$router.push(url)
            },

            /**
             * 获取活动详情
             * **/
            async getDetail(){
                console.log('接收参数',this.$route.query.id)
                let params = {
                    activityId:this.$route.query.id
                };

                if(this.$route.query.id){
                    let getDetailInfo = await request('/api/open/activity/selectActivityInfoById',
                    'post',params,'application/x-www-form-urlencoded')
                // loading.hide()
                    let res = result(getDetailInfo)
                    console.log(getDetailInfo)

                    this.theme = res.activityName;
                    this.activityAddress = res.activityAddress;
                    this.activityContent= res.activityDetails;
                    this.activityTime = res.activityTime;
                }



            },
		}
	}
</script>

